<template>
  <div class="wrapper">
    <div class="logo">
      <img
        src="data:image/png;base64,"
        alt=""
      />
    </div>
    <div class="text-center mt-4 name">Dashboard</div>
    <form @submit.prevent="handleSubmit" class="p-3 mt-3">
      <div class="form-field d-flex align-items-center">
        <span class="far fa-user"></span>
        <input
          type="text"
          name="userName"
          id="userName"
          placeholder="Username"
          v-model="username"
        />
      </div>
      <div class="form-field d-flex align-items-center">
        <span class="fas fa-key"></span>
        <input
          type="password"
          name="password"
          id="pwd"
          placeholder="Password"
          v-model="password"
        />
      </div>
      <button type="submit" class="btn mt-3">Login</button>
    </form>
    <!-- <div class="text-center fs-6">
      <a href="#">Forget password?</a> or <a href="#">Sign up</a>
    </div> -->
  </div>
</template>

<script>
import login from "../../components/Api/Api";
import axios from "axios";

export default {
  emits: ["setloading"],
  data() {
    return {
      data: "",
      username: "",
      password: "",
      role: localStorage.getItem("role"),
    };
  },
  created() { console.clear();
    this.$emit("setloading", false);
    localStorage.clear();
    // console.log(localStorage);
    // console.clear();
  },
  methods: {
    async handleSubmit() {
      // this.isloading = true;
      // console.log(this);
      const url = "https://writer-production.up.railway.app/token";
      let params = new URLSearchParams();
      params.append("username", this.username);
      params.append("password", this.password);

      const config = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      };
      // console.log(url, params, config);
      // console.log(this);
      axios.post(url, params, config).then(
        (response) => {
          console.log(response);
          if (response.status == 200) {
            // console.log("200 boldi");
            // console.log(response.data);
            // console.log();
            localStorage.setItem("access_token", response.data.access_token);
            // localStorage.setItem("branch_id", response.data.branch_id);
            localStorage.setItem("role", response.data.role);
            if (localStorage.role == "admin") {
              this.$router.push("/orders");
              // console.log("adminga kiridik");
              // window.location.reload();
              console.log(localStorage);
            } else if (localStorage.role == "worker") {
              this.$router.push("/mystart");
              // console.log("workerga kiridik");
              // window.location.reload();
            }
          } else if (response.status == 401) {
            // console.clear();
            // console.log("401 boldi");
            //
          }
        },
        (error) => {
          // console.log(error.response.status);
          if (error.response.status == 401) {
            localStorage.removeItem("role");
            localStorage.removeItem("access_token");
            Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            })
              .fire({
                icon: "error",
                title: "Incorrect username or password!",
              })
              .then(() => {
                if (window.location.pathname == "/") {
                  console.log();
                } else {
                  window.location.href = "/";
                }
              });
          }
        }
      );
    },
  },
};
</script>

